/* === Google Fonts & Global Styles === */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

:root {
  --primary-color: #4A90E2; 
  --primary-hover-color: #357ABD;
  --background-color: #F4F7FA;
  --card-bg-color: #FFFFFF;
  --text-color-primary: #2D3748;
  --text-color-secondary: #718096;
  --border-color: #E2E8F0;
  --shadow-sm: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --border-radius-md: 12px;
  --border-radius-lg: 20px;
  --transition-speed: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* { box-sizing: border-box; }
.container { max-width: 1280px; margin: 0 auto; padding: 0 24px; }

/* === Header === */
.app-header {
  background: var(--card-bg-color);
  padding: 16px 40px;
  position: sticky;
  top: 0;
  z-index: 1000;
  border-bottom: 1px solid var(--border-color);
}
.logo { font-size: 1.8em; font-weight: 700; color: var(--primary-color); }

/* === Hero Section === */
.hero {
  text-align: center;
  padding: 80px 20px;
  margin-top: 40px;
  border-radius: var(--border-radius-lg);
  border: 1px solid var(--border-color);
  background-color: #ffffff;
  background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23d4e1f0' fill-opacity='0.4'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}
.hero-content h1 { font-size: 3rem; font-weight: 700; margin-bottom: 16px; }
.hero-content p { font-size: 1.125rem; color: var(--text-color-secondary); max-width: 600px; margin: 0 auto 32px; }
.search-bar {
  display: flex;
  background-color: var(--card-bg-color);
  padding: 8px;
  border-radius: 50px;
  max-width: 550px;
  margin: 0 auto;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-color);
}
.search-bar input { border: none; outline: none; width: 100%; font-size: 1rem; padding: 0 20px; background: transparent; }
.btn {
  padding: 12px 30px; border: none; border-radius: 50px; font-size: 1rem;
  font-weight: 600; cursor: pointer; transition: all var(--transition-speed);
  background: var(--primary-color); color: white;
}
.btn:hover { background: var(--primary-hover-color); box-shadow: var(--shadow-md); transform: translateY(-2px); }

/* === Category Section === */
.category-section { padding: 80px 0; }
.category-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 32px;
}
.category-card {
  display: flex; flex-direction: column; align-items: center; justify-content: center;
  gap: 15px;
  padding: 32px 20px;
  border-radius: var(--border-radius-md);
  background: var(--card-bg-color); box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color); cursor: pointer;
  transition: all var(--transition-speed);
}
.category-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-md);
  color: var(--primary-color);
  border-color: var(--primary-color);
}
.category-card img {
  width: 56px;
  height: 56px;
}
.category-card h3 {
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0;
}

/* === Results Section (UPDATED) === */
.results-section { padding: 60px 0; }
.results-section h2 { font-size: 2rem; text-align: center; margin-bottom: 40px; }
.card-grid {
  display: grid;
  /* Make cards larger */
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr)); 
  gap: 40px;
}
.summary-card {
  background: var(--card-bg-color);
  border-radius: var(--border-radius-md);
  box-shadow: var(--shadow-md); /* Stronger shadow by default */
  transition: all var(--transition-speed);
  cursor: pointer;
  overflow: hidden;
}
.summary-card:hover { 
  transform: translateY(-8px); 
  box-shadow: var(--shadow-lg); /* Even stronger shadow on hover */
}
.summary-card img { 
  width: 100%; 
  height: 250px; /* Much larger image height */
  object-fit: cover; 
}
.summary-card-content { 
  padding: 24px; /* More padding */
}
.summary-card h4 { 
  margin: 0 0 8px; 
  font-size: 1.3rem; /* Larger title */
}
.summary-card p { 
  font-size: 1rem; /* Larger secondary text */
  color: var(--text-color-secondary); 
  margin: 0; 
}
.summary-card .rating { 
  font-weight: 600; 
  color: #f39c12; 
  font-size: 1.1rem; /* Larger rating */
}

/* === Other Styles (Feedback, Modal, Footer) === */
.feedback-container{display:flex;justify-content:center;align-items:center;min-height:300px;font-size:1.1rem;color:var(--text-color-secondary)}.loader{border:4px solid #f3f3f3;border-top:4px solid var(--primary-color);border-radius:50%;width:40px;height:40px;animation:spin 1s linear infinite}@keyframes spin{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}.modal-overlay{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,.6);display:flex;justify-content:center;align-items:center;z-index:2000;backdrop-filter:blur(8px)}.modal-content{background:#fff;padding:30px;border-radius:var(--border-radius-lg);max-width:600px;width:90%;max-height:90vh;overflow-y:auto;position:relative;box-shadow:var(--shadow-lg);animation:slide-up .3s ease}@keyframes slide-up{from{transform:translateY(20px);opacity:0}to{transform:translateY(0);opacity:1}}.close-button{position:absolute;top:15px;right:15px;background:#eee;border:none;border-radius:50%;width:30px;height:30px;font-size:1.5rem;cursor:pointer;line-height:30px}.modal-image{width:100%;height:250px;object-fit:cover;border-radius:var(--border-radius-md);margin-bottom:20px}.modal-content h2{margin-top:0;font-size:1.8rem}.detail-section{margin-top:24px}.detail-section h4{border-bottom:1px solid var(--border-color);padding-bottom:10px;margin-bottom:15px;font-size:1.2rem}.seating-chart{display:grid;grid-template-columns:repeat(auto-fit,minmax(35px,1fr));gap:5px}.screen{grid-column:1 / -1;text-align:center;background:#333;color:#fff;padding:5px;border-radius:5px;margin-bottom:15px;font-weight:600}.seat{font-size:.7rem;padding:8px 0;text-align:center;border-radius:4px;cursor:pointer;transition:background-color .2s;border:1px solid var(--border-color)}.seat.available{background-color:#E8F5E9;color:#2E7D32}.seat.booked{background-color:#FFEBEE;color:#C62828}.seat.available:hover{background-color:#C8E6C9}.menu-list{list-style:none;padding:0}.menu-list li{display:flex;justify-content:space-between;padding:12px 0;border-bottom:1px solid var(--border-color)}.menu-list li.popular{font-weight:600}.menu-list .price{color:var(--primary-color);font-weight:600}.slot-list{display:flex;flex-wrap:wrap;gap:12px}.slot{background:var(--background-color);padding:10px 15px;border-radius:8px;text-align:center;border:1px solid var(--border-color)}.slot .time{font-weight:600;display:block}.slot .spots{font-size:.9rem;color:var(--text-color-secondary)}.footer{text-align:center;padding:40px 20px;color:var(--text-color-secondary);font-size:0.9rem}